bizagi.workportal.widgets.widget.extend("bizagi.workportal.widgets.project.activity.description", {}, {

  /*
                                                                                                         *   Constructor
                                                                                                         */
  init: function init(workportalFacade, dataService, params) {
    var self = this;

    // Call base
    self._super(workportalFacade, dataService, params);

    params = params || {};

    //Load templates
    self.loadTemplates({
      "project-activity-description": bizagi.getTemplate("bizagi.workportal.desktop.widget.project.activity.description").concat("#project-activity-description") });

  },

  /**
      * Renders the template defined in the widget
      */
  renderContent: function renderContent() {
    var self = this;
    var template = self.getTemplate("project-activity-description");
    self.content = template.render();
    return self.content;
  },

  /**
      * links events with handlers
      */
  postRender: function postRender() {
    var self = this;
    self.description = self.getWorkitemDescription();
    if (self.description) {
      self.callTaskDescription();
    } else {
      self.content = $('<div />');
    }
  },

  /**
      * 
      * Call task description widget 
      */

  callTaskDescription: function callTaskDescription() {
    var self = this;
    var width = 310;
    var taskDescriptionParams = {
      container: $("#ui-bizagi-wp-project-activity-task_description_container", self.content),
      description: self.description,
      width: width };

    self.taskDescriptionWidget = new bizagi.workportal.widgets.task.description(self.workportalFacade, self.dataService, taskDescriptionParams);
    self.taskDescriptionWidget.render();
  },

  getWorkitemDescription: function getWorkitemDescription() {
    var self = this;
    var params = self.params || {};
    var idWorkitem = params.idWorkitem || 0;
    var currentState = params.currentState || [];
    var description = "";

    for (var i = 0; i < currentState.length; i++) {
      if (idWorkitem == currentState[i].idWorkItem) {
        description = currentState[i].tskDescription;
        break;
      }
    }

    return description;
  },

  /**
      * Clean widget
      */
  clean: function clean() {

  } });


bizagi.injector.register('bizagi.workportal.widgets.project.activity.description', ['workportalFacade', 'dataService', bizagi.workportal.widgets.project.activity.description], true);