/*
 *   Name: Bizagi Workportal Desktop Project Case State Controller
 *   Author: Elkin Fernando Siabato Cruz
 */

bizagi.workportal.widgets.widget.extend("bizagi.workportal.widgets.project.caseState", {}, {
  /*
                                                                                              *   Constructor
                                                                                              */
  init: function init(workportalFacade, dataService, params) {
    var self = this;
    // Call base
    self._super(workportalFacade, dataService, params);

    self.contextsSidebarActivity = params.contextsSidebarActivity;

    //Regional
    self.datePickerRegional = bizagi.localization.getResource("datePickerRegional");

    //Load templates
    self.loadTemplates({
      "project-caseState": bizagi.getTemplate("bizagi.workportal.desktop.widget.project.caseState").concat("#project-caseState-wrapper"),
      "project-caseState-remaining": bizagi.getTemplate("bizagi.workportal.desktop.widget.project.caseState").concat("#project-caseState-remaining"),
      "project-cancel-case-button": bizagi.getTemplate("bizagi.workportal.desktop.widget.project.caseState").concat("#project-caseState-cancel-case-button") });

  },

  /**
      * Renders the template defined in the widget
      */
  renderContent: function renderContent() {
    var self = this;
    self.content = $("<div></div>");
    return self.content;
  },

  /*
      * links events with handlers
      */
  postRender: function postRender() {
    var self = this;
    var cntxtSidebarActivity = self.contextsSidebarActivity || [];
    //Handlers
    cntxtSidebarActivity.forEach(function (context) {
      self.sub(context, $.proxy(self.updateView, self));
    });
  },

  /**
      * Update view when load case
      * @param event
      * @param params
      */
  updateView: function updateView(event, params) {
    var self = this;
    $.extend(self.params, params.args);
    var args = params.args;
    self.getContent().empty();

    var argsTemplate = {};

    //is Favorite Case
    argsTemplate.isFavoriteCase = "off";
    if (args.isFavorite && JSON.parse(args.isFavorite)) {
      argsTemplate.isFavoriteCase = "bz-icon-star";
    } else
    {
      argsTemplate.isFavoriteCase = "bz-icon-star-outline";
    }

    self.showCaseNumber(args, argsTemplate);

    //Begin Calculated Remaining Date
    if (args.creationDate) {
      var MOMENT_FORMAT = "MM/DD/yyyy H:mm";
      var timeZoneName = bizagi.currentUser.timeZoneName;
      var creationDate = args.creationDate;
      var creationDateObject = self.getDateWithTimezone(MOMENT_FORMAT, timeZoneName, creationDate);

      argsTemplate.creationDateFormat = self.getFormattedDate(creationDateObject);

      var solutionDateObject = self.getDateWithTimezone(MOMENT_FORMAT, timeZoneName, args.solutionDate);
      argsTemplate.solutionDateFormat = self.getFormattedDate(solutionDateObject);

      var currentDate = self.getDateWithTimezone(MOMENT_FORMAT, timeZoneName);
      self.calculateDataForTemplate(argsTemplate, creationDateObject, currentDate, solutionDateObject, args);
    }
    $(self.getContent()).on("click", ".show-diagram-case", function () {
      var isAdhoc = false;
      if (!self.params.isAdhocProcess || typeof self.params.isAdhocProcess === 'undefined') {
        isAdhoc = false;
      } else {
        isAdhoc = true;
      }
      if (isAdhoc) {
        self.showGraphicQuery({ idCase: self.params.idCaseForGraphicQuery || self.params.idCase, idWorkflow: self.params.idWorkFlow, isAdhoc: true, idProcess: self.params.idAdhocProcess });
      } else {
        self.showGraphicQuery({ idCase: self.params.idCaseForGraphicQuery || self.params.idCase, idWorkflow: self.params.idWorkflow });
      }

    });

    $(self.getContent()).on("click", ".show-process-help", function () {
      self.showHelpUrl(self.params.helpUrl);
    });

  },

  /**
      * Calculate percent bar
      * @param argsTemplate
      * @param creationDateObject
      * @param currentDate
      * @param solutionDateObject
      * @param args
      */
  calculateDataForTemplate: function calculateDataForTemplate(argsTemplate, creationDateObject, currentDate, solutionDateObject, args) {
    var self = this;
    var $content = self.getContent();
    var MOMENT_FORMAT = "MM/DD/yyyy H:mm";
    var timeZoneName = bizagi.currentUser.timeZoneName;
    var paramsEffectiveDuration = {};
    paramsEffectiveDuration.idUser = bizagi.currentUser.idUser;
    if (bizagi.util.parseBoolean(args.isOpen)) {
      paramsEffectiveDuration.fromDate = creationDateObject.getTime();
      paramsEffectiveDuration.toDate = currentDate.getTime();
    } else
    {
      paramsEffectiveDuration.fromDate = solutionDateObject.getTime();
      paramsEffectiveDuration.toDate = currentDate.getTime();
    }

    var differenceCreationToCurrent = paramsEffectiveDuration.toDate - paramsEffectiveDuration.fromDate;
    var actualDate = self.getDateWithTimezone(MOMENT_FORMAT, timeZoneName);
    var relativeTime = bizagi.util.dateFormatter.getRelativeTime(new Date(actualDate.getTime() - differenceCreationToCurrent), null, false, timeZoneName);
    if (bizagi.util.parseBoolean(args.isOpen)) {
      argsTemplate.relativeTimeState = bizagi.localization.getResource("workportal-project-case-state-opened").replace("%s", relativeTime);
      argsTemplate.colorStateCase = "Opened";
    } else
    {
      argsTemplate.relativeTimeState = bizagi.localization.getResource("workportal-project-case-state-closed").replace("%s", relativeTime);
      argsTemplate.colorStateCase = "Closed";
    }

    argsTemplate.percentCompleteBar = 100;
    argsTemplate.isOpen = bizagi.util.parseBoolean(args.isOpen);
    //Update widget
    var contentTemplate = self.getTemplate("project-caseState-remaining");
    contentTemplate.
    render($.extend(args, argsTemplate)).
    appendTo($("#ui-bizagi-remaining", $content));
  },


  /**
      * Show cancel case button
      * @param {JSON} args 
      * @param {Template} argsTemplate 
      * @param {Boolean} showCancelCase 
      */
  showCancelCase: function showCancelCase(args, argsTemplate, _showCancelCase) {
    var self = this;
    var $content = self.getContent();
    var contentTemplate = self.getTemplate("project-cancel-case-button");

    argsTemplate.buttonCancelCase = _showCancelCase;
    contentTemplate.render($.extend(args, argsTemplate)).appendTo($("#ui-bizagi-cancel-case", $content));
    $("#ui-bizagi-wp-project-activity-cancel-case-button > a", self.content).click(function () {
      var widgetParameters = {
        widgetName: bizagi.workportal.widgets.widget.BIZAGI_WORKPORTAL_WIDGET_CANCEL_CASE,
        maximize: false,
        dialogClass: 'bz-cancel-case-widget',
        idWorkitem: self.params.idWorkitem,
        idCase: self.params.idCase,
        modalParameters: {
          refreshInbox: false,
          title: bizagi.localization.getResource("workportal-widget-cancel-case-title"),
          width: 420,
          height: 400 },

        closeVisible: false };

      self.publish("showDialogWidget", widgetParameters);
    });
  },

  /**
      * Show case number
      * @param args
      * @param argsTemplate
      */
  showCaseNumber: function showCaseNumber(args, argsTemplate) {
    var self = this;
    var $content = self.getContent();
    var contentTemplate = self.getTemplate("project-caseState");
    var isAdhoc = false;
    if (!this.params.isAdhocProcess || typeof this.params.isAdhocProcess === 'undefined') {
      isAdhoc = false;
    } else
    {
      isAdhoc = this.params.isAdhocProcess;
    }

    self.params.helpUrl = bizagi.util.validateUrlWellFormed(self.params.helpUrl);

    contentTemplate.
    render(
    $.extend(
    args,
    argsTemplate,
    {
      security: bizagi.menuSecurity,
      isAdhocProcess: isAdhoc,
      urlHelp: !bizagi.util.isEmpty(self.params.helpUrl) })).



    appendTo($content);
    self._startShowCancelCase(args, argsTemplate);
    //Handle events
    $(".case-summary-favorite", $content).on("click", $.proxy(self.onClickFavorite, self));
    // Set back and next case navigation
    $(".case-summary-back", self.content).on("click", $.proxy(self.goToCase, self, 'back'));
    $(".case-summary-next", self.content).on("click", $.proxy(self.goToCase, self, 'next'));

    self.showBackNextNavigation();
  },

  /**
      * wait for showCancelCase value from Navigator widget
      * @param {JSON} args 
      * @param {Template} argsTemplate 
      */
  _startShowCancelCase: function _startShowCancelCase(args, argsTemplate) {
    var self = this,
    _navigatorWidget = bizagi.injector.get("bizagi.workportal.widgets.navigator"),
    _start = self.showCancelCase.bind(self, args, argsTemplate);

    _navigatorWidget.getShowCancelCase().done(_start);
  },

  /**
      * Get Format date
      * @param dateObj
      * @returns {string}
      */
  getFormattedDate: function getFormattedDate(dateObj) {
    var self = this;
    var monthsNames = self.datePickerRegional.monthNames;
    var shortMonth = dateObj.getMonth();
    var bizagiLanguage = BIZAGI_LANGUAGE;
    if (bizagiLanguage != null && bizagiLanguage != undefined && bizagiLanguage.length > 0 && bizagiLanguage.substring(0, 2) == "fr")
    return $.datepicker.formatDate("dd", dateObj) + " " + monthsNames[shortMonth];else

    return monthsNames[shortMonth] + " " + $.datepicker.formatDate("dd", dateObj);
  },

  /***
      * Events
      */
  onClickFavorite: function onClickFavorite(event) {
    event.preventDefault();
    var self = this;
    var favoriteOptions = {};
    if ($(event.target).hasClass("bz-icon-star-outline")) {
      favoriteOptions = {
        idObject: self.params.idCase,
        favoriteType: "CASES" };

      $.when(self.dataService.addFavorite(favoriteOptions)).done(function (favoritesData) {
        self.params.guidFavorite = favoritesData.idFavorites;
        $(event.target).removeClass("bz-icon-star-outline");
        $(event.target).addClass("bz-icon-star");
      });
    } else
    {
      favoriteOptions = {
        idObject: self.params.guidFavorite,
        favoriteType: "CASES" };

      $.when(self.dataService.delFavorite(favoriteOptions)).done(function () {
        $(event.target).addClass("bz-icon-star-outline");
        $(event.target).removeClass("bz-icon-star");
      });
    }
  },

  showHelpUrl: function showHelpUrl(urlHelp) {
    window.open(urlHelp);
  },

  /**
      * Call service effective duration by schedule work
      * @param params
      * @return {*}
      */
  callGetEffectiveDuration: function callGetEffectiveDuration(params) {
    var self = this;
    var d = $.Deferred();

    if (params.fromDate) {
      $.when(
      self.dataService.getEffectiveDuration(params)).
      done(function (data) {
        d.resolve(data);
      });
    } else
    {
      d.resolve(null);
    }

    return d.promise();
  },

  /**
      * Clean plugins and events
      */
  clean: function clean() {
    var self = this;
    $(".show-diagram-case", self.getContent()).off();
    var cntxtSidebarActivity = self.contextsSidebarActivity || [];
    this.params = {};
    cntxtSidebarActivity.forEach(function (context) {
      self.unsub(context, $.proxy(self.updateView, self));
    });
  },

  goToCase: function goToCase(action) {
    var self = this;
    var idCase = action == 'back' ? self.getBackCase() : self.getNextCase();
    if (idCase > 0) {
      self.publish("executeAction", {
        action: bizagi.workportal.actions.action.BIZAGI_WORKPORTAL_ACTION_ROUTING,
        idCase: idCase });

    }
  },

  getListKey: function getListKey() {
    var self = this;
    bizagi.lstIdCases = bizagi.lstIdCases || {};
    if (bizagi.lstIdCases.length > 1) {
      for (var i = 0; i < bizagi.lstIdCases.length; i++) {
        if (self.params.idCase == bizagi.lstIdCases[i]) {
          return i;
        }
      }
    }
    return -1;
  },

  getNextCase: function getNextCase() {
    var self = this;
    var nextIdCase = 0;
    var key = self.getListKey();

    // Check if last element
    if (bizagi.lstIdCases.length == key + 1 || bizagi.lstIdCases.length == 1) {
      nextIdCase = -1;
    } else {
      nextIdCase = bizagi.lstIdCases[key + 1];
    }

    return nextIdCase;
  },

  getBackCase: function getBackCase() {
    var self = this;
    var prevIdCase = 0;
    var key = self.getListKey();

    // Check if first element
    if (key == 0) {
      prevIdCase = -1;
    } else {
      prevIdCase = bizagi.lstIdCases[key - 1] || -1;
    }

    return prevIdCase;
  },

  showBackNextNavigation: function showBackNextNavigation() {
    var self = this;
    var nextCase = self.getNextCase();
    var backCase = self.getBackCase();
    nextCase <= 0 ? $(".case-summary-next", self.content).hide() : $(".case-summary-next", self.content).show();
    backCase <= 0 ? $(".case-summary-back", self.content).hide() : $(".case-summary-back", self.content).show();

    if (nextCase <= 0 && backCase <= 0) {
      $(".content-right-sidebar").removeClass("bz-state-number--active-case");
      $(".bz-case-navigation").hide();
    } else
    {
      $(".content-right-sidebar").addClass("bz-state-number--active-case");
      $(".bz-case-navigation").show();
    }
  } });


bizagi.injector.register("bizagi.workportal.widgets.project.caseState", ["workportalFacade", "dataService", bizagi.workportal.widgets.project.caseState]);