/*
 *   Name: Bizagi Workportal Desktop Project Activity State Controller
 *   Author: Elkin Fernando Siabato Cruz
 */

bizagi.workportal.widgets.widget.extend("bizagi.workportal.widgets.project.activityState", {}, {
  /*
                                                                                                  *   Constructor
                                                                                                  */
  init: function init(workportalFacade, dataService, params) {
    var self = this;

    // Call base
    self._super(workportalFacade, dataService, params);

    self.contextsSidebarActivity = params.contextsSidebarActivity;

    //Regional
    self.datePickerRegional = bizagi.localization.getResource("datePickerRegional");

    //Load templates
    self.loadTemplates({
      "project-activityState-wrapper": bizagi.getTemplate("bizagi.workportal.desktop.widget.project.activityState").concat("#project-activityState-wrapper") });

  },

  /*
      * Renders the template defined in the widget
      */
  renderContent: function renderContent() {
    var self = this;
    self.content = $("<div></div>");
    return self.content;
  },

  /*
      * links events with handlers
      */
  postRender: function postRender() {
    var self = this;
    //Handlers
    self.contextsSidebarActivity.forEach(function (context) {
      self.sub(context, $.proxy(self.updateView, self));
    });

  },

  updateView: function updateView(event, params) {
    var self = this,
    args = params.args;
    self.params = args;
    self.getContent();
    var timeZoneName = bizagi.currentUser.timeZoneName;
    var argsTemplate = {};
    var currentState = args.currentState || [];

    var currentWorkitem = currentState.filter(function (objectWorkItem) {return objectWorkItem.idWorkItem == args.idWorkitem;})[0];


    if (currentWorkitem) {
      argsTemplate.activityName = currentWorkitem.state;
      argsTemplate.activityIsEvent = bizagi.util.parseBoolean(currentWorkitem.isEvent);

      var MOMENT_FORMAT = "MM/DD/yyyy H:mm";
      var initDateObject = self.getDateWithTimezone(MOMENT_FORMAT, timeZoneName, currentWorkitem.entryDateWorkItem);
      if (initDateObject.getTime) {
        argsTemplate.initDateFormat = self.getFormattedDate(initDateObject);
      }

      var currentDate = self.getDateWithTimezone(MOMENT_FORMAT, timeZoneName);
      var estimatedSolutionDateObject = self.getDateWithTimezone(MOMENT_FORMAT, timeZoneName, currentWorkitem.estimatedSolutionDate);
      argsTemplate.estimatedSolutionDateFormat = "";

      if (initDateObject.getTime && estimatedSolutionDateObject.getTime && currentWorkitem) {
        self.calculateDataForTemplate(currentWorkitem, argsTemplate, initDateObject, currentDate, estimatedSolutionDateObject, currentState);
      } else
      {
        //Update widget
        self.updateTemplateWidget(argsTemplate, currentState);
      }
    }
  },

  initilizeActionMenu: function initilizeActionMenu(currentState) {
    var self = this;
    var allowsReassign = false;

    if (currentState && currentState.length > 0) {
      for (j = 0; j < currentState.length; ++j) {
        if (currentState[j] && self.params && currentState[j].idWorkItem == self.params.idWorkitem) {
          allowsReassign = bizagi.util.parseBoolean(currentState[j].allowsReassign) || false;
        }
      }
    }
    var btReassing = $("#bt-case-action-reassing", self.content);
    var btShareActivity = $("#ui-bizagi-wp-project-activity-button:not(.ui-bizagi-wp-project-activity-shared)", self.content);

    if (allowsReassign) {
      btReassing.show();
    }
    btReassing.on("click", function () {
      self.onClickMenuReasign();
    });
    btShareActivity.on("click", function () {
      self.onClickShareActivity();
    });
  },
  /**
       * Executed when user reasign case
       */
  onClickMenuReasign: function onClickMenuReasign() {
    var self = this;
    var idCase = self.params.idCase;
    var idWorkItem = self.params.idWorkitem,
    onCloseSaveDialogBox = function onCloseSaveDialogBox() {
      self.publish("closeCurrentDialog");
    };
    bizagi.loader.startAndThen("admin").then(function () {
      $.when(self.publish("showDialogWidget", {
        widgetName: bizagi.workportal.widgets.widget.BIZAGI_WORKPORTAL_WIDGET_REASSIGN_CASE,
        data: {
          "idCase": idCase,
          "idWorkItem": idWorkItem,
          closeDialogWidget: onCloseSaveDialogBox },

        closeVisible: false,
        maximize: true,
        modalParameters: {
          title: bizagi.localization.getResource("render-actions-reassign"),
          width: "910px",
          id: "CaseAdmin" } }));


    });
  },

  /**
      * let share the activity
      */
  onClickShareActivity: function onClickShareActivity() {
    var self = this;
    var params = {
      "idWorkItem": self.params.idWorkitem };

    var button = $("#ui-bizagi-wp-project-activity-button", self.content);
    button.attr("disabled", true);
    $.when(self.dataService.getShareActivity(params)).done(function (data) {
      var notifier = window.parent.bizagi.injector.get('notifier');
      if (data.value === true) {
        button.addClass("ui-bizagi-wp-project-activity-shared").
        unbind("click");
        notifier.showSucessMessage(bizagi.localization.getResource("workportal-project-activity-success-message"), '');
      } else {
        notifier.showErrorMessage(bizagi.localization.getResource("workportal-project-activity-error-message"), '');
      }
    }).always(function () {
      button.attr("disabled", false);
    });
  },

  calculateDataForTemplate: function calculateDataForTemplate(currentWorkitem, argsTemplate, creationDateObject, currentDate, estimatedSolutionDateObject, currentState) {
    var self = this;
    var MOMENT_FORMAT = "MM/DD/yyyy H:mm";
    var timeZoneName = bizagi.currentUser.timeZoneName;
    var diffMillisecondsCurrentToEstimated = currentDate.getTime() - estimatedSolutionDateObject.getTime();
    var diffMillisecondsCreationToSolution = creationDateObject.getTime() - estimatedSolutionDateObject.getTime();

    var paramsEffectiveDurationOpened = {
      idUser: bizagi.currentUser.idUser,
      fromDate: creationDateObject.getTime(),
      toDate: self.getDateWithTimezone(MOMENT_FORMAT, timeZoneName).getTime() };


    var currentDateExceededEstimatedDate = false;
    var paramsEffectiveDuration = {
      idUser: bizagi.currentUser.idUser,
      fromDate: currentDate.getTime(),
      toDate: estimatedSolutionDateObject.getTime() };

    var paramsEffectiveDurationInitialToEstimated = {};
    if (diffMillisecondsCurrentToEstimated > 0) {//Exceeded limit estimatedSolution
      paramsEffectiveDuration.fromDate = estimatedSolutionDateObject.getTime();
      paramsEffectiveDuration.toDate = currentDate.getTime();
      currentDateExceededEstimatedDate = true;
    } else
    {
      paramsEffectiveDurationInitialToEstimated.idUser = bizagi.currentUser.idUser;
      paramsEffectiveDurationInitialToEstimated.fromDate = creationDateObject.getTime();
      paramsEffectiveDurationInitialToEstimated.toDate = estimatedSolutionDateObject.getTime();
    }

    var differenceCreatedToCurrent = paramsEffectiveDurationOpened.toDate - paramsEffectiveDurationOpened.fromDate;
    var differenceCurrentToEstimated = paramsEffectiveDuration.toDate - paramsEffectiveDuration.fromDate;
    var differenceInitialToEstimated = diffMillisecondsCurrentToEstimated > 0 ? 0 : paramsEffectiveDurationInitialToEstimated.toDate - paramsEffectiveDurationInitialToEstimated.fromDate;

    var actualDate = self.getDateWithTimezone(MOMENT_FORMAT, timeZoneName);
    var relativeTime = bizagi.util.dateFormatter.getRelativeTime(new Date(actualDate.getTime() - differenceCreatedToCurrent), null, false, timeZoneName);
    argsTemplate.colorState = currentWorkitem.colorState;
    argsTemplate.relativeTimeState = bizagi.localization.getResource("workportal-project-activity-state-opened").replace("%s", relativeTime);
    argsTemplate.percentCompleteBar = 100;

    if (diffMillisecondsCreationToSolution === 0) {
      argsTemplate.showEstimatedSolutionDate = false;
    } else
    {
      argsTemplate.showEstimatedSolutionDate = true;
      if (currentDateExceededEstimatedDate === false) {
        var percentBar = self.getPercentBar(differenceInitialToEstimated / 1000 / 60, differenceCurrentToEstimated / 1000 / 60);
        argsTemplate.percentCompleteBar = percentBar.percent;
      }
    }

    if (currentWorkitem) {
      argsTemplate.isEvent = bizagi.util.parseBoolean(currentWorkitem.isEvent);
      argsTemplate.canShareActivity = currentWorkitem.canShareActivity;
      argsTemplate.isShared = currentWorkitem.isShared;
    }

    argsTemplate.estimatedSolutionDateFormat = self.getFormattedDate(estimatedSolutionDateObject);

    //Update widget
    self.updateTemplateWidget(argsTemplate, currentState);
  },

  getPercentBar: function getPercentBar(totalMinutes, sectionMinutes) {
    var response = {};
    response.percent = Math.round((1 - sectionMinutes / totalMinutes) * 100);
    return response;
  },


  updateTemplateWidget: function updateTemplateWidget(argsTemplate, currentState) {
    var self = this,
    $content = self.getContent().empty();
    if (bizagi.override.enableShareActivity === false) {
      argsTemplate.canShareActivity = false;
      argsTemplate.isShared = false;
    }
    //Update widget
    var contentTemplate = self.getTemplate("project-activityState-wrapper");
    contentTemplate.
    render(argsTemplate).appendTo($content);

    if (currentState.length > 0) {
      self.initilizeActionMenu(currentState);
    }
    self.shareActivityTooltip();
  },

  /**
      * show tooltip when the activity is shared or can be
      */
  shareActivityTooltip: function shareActivityTooltip() {
    var self = this;
    var activityButton = $("#ui-bizagi-wp-project-activity-button", self.content);
    activityButton.on('mouseenter', function () {
      var texto = activityButton.hasClass("ui-bizagi-wp-project-activity-shared") ? "workportal-project-activity-shared" : "workportal-project-activity-share";
      activityButton.attr("title", bizagi.localization.getResource(texto));
    });
    activityButton.tooltip({
      tooltipClass: "custom-tooltip-styling-bottom-bar",
      position: {
        my: 'right top',
        at: 'left top',
        of: activityButton,
        collision: 'flip' } });


  },

  getFormattedDate: function getFormattedDate(dateObj) {
    var self = this;
    var monthsNames = self.datePickerRegional.monthNames;
    var shortMonth = dateObj.getMonth();
    var bizagiLanguage = BIZAGI_LANGUAGE;
    if (bizagiLanguage != null && bizagiLanguage != undefined && bizagiLanguage.length > 0 && bizagiLanguage.substring(0, 2) == "fr")
    return $.datepicker.formatDate("dd", dateObj) + " " + monthsNames[shortMonth];else

    return monthsNames[shortMonth] + " " + $.datepicker.formatDate("dd", dateObj);
  },

  clean: function clean() {
    var self = this;
    var cntxtSidebarActivity = self.contextsSidebarActivity || [];
    this.params = {};
    cntxtSidebarActivity.forEach(function (context) {
      self.unsub(context, $.proxy(self.updateView, self));
    });
  },

  /**
      * Call services
      */
  callGetEffectiveDuration: function callGetEffectiveDuration(params) {
    var self = this;
    var d = $.Deferred();

    //Fix .NET, multiple call service with same data in a few milliseconds
    setTimeout(function () {
      if (params.fromDate) {
        $.when(
        self.dataService.getEffectiveDuration(params)).
        done(function (data) {
          d.resolve(data);
        });
      } else
      {
        d.resolve(null);
      }
    }, 200);


    return d.promise();
  } });


bizagi.injector.register("bizagi.workportal.widgets.project.activityState", ["workportalFacade", "dataService", bizagi.workportal.widgets.project.activityState]);